(function ($) {
    $(() => {
        const modal = $('.js-slider-modal');
        const modalSlider = $('.js-panorama-slider-modal');

        if (!modal.length) {
            return;
        }

        $('.js-slider-modal-image').on('click', (e) => {
            e.preventDefault();
            $('html, body').addClass('overflow-hidden');
            const top = document.documentElement.scrollTop;
            modal.addClass('is-active').css({ top });
            modalSlider.slick({
                slidesToShow: 1,
                slidesToScroll: 1,
                infinite: true,
                prevArrow: $('.js-panorama-slider-modal-prev'),
                nextArrow: $('.js-panorama-slider-modal-next'),
                dots: true,
                buttons: false,
            });
        });

        $('.js-close-panorama-slider-modal').on('click', function(e){
            e.preventDefault();
            $('html, body').removeClass('overflow-hidden');
            modal.removeClass('is-active');
            modalSlider.slick('unslick');;
        });
    });
})(jQuery);
