(function ($) {
    $(() => {
        const subscriptionForm = $('.js-newsletter-subscribe');

        if (!subscriptionForm.length) {
            return;
        }

        $(document).on('focus js-focus change', '.js-input-group', function (e) {
            $(this).children('.js-placeholder').addClass('active');
        });

        $(document).on('blur', '.js-input-group', function (e) {
            if($(this).children('input').val() == ""){
                $(this).children('.js-placeholder').removeClass('active');
            }
        });
    });
})(jQuery);
